.topbarContainer {
    background-color: #2756ad;
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;

}

.topbarLeft {
    flex: 3 1;
}

.topbarCenter {
    flex: 5 1;
    text-align: center;
}

.topbarRight {
    flex: 4 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
}

.logo {
    font-size: 24px;
    color: white;
    font-weight: bold;
    margin-left: 20px;
}

.searchbar {
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 25px;
    display: flex;
    align-items: center;
}

.searchIcon {
    font-size: 20px !important;
    margin-left: 10px;
    margin-right: 5px;
}

.searchInput {
    border: none;
    width: 80%;
}

.searchInput:focus {
    outline: none;
}

.topbarItemIcons {
    display: flex;
}

.topbarIconItem {
    margin-right: 15px;
    /* cursor: pointer;
    position: relative; */
