    align-items: center;
}

.TopbarCoinIcon {
    color: #f9ca02;
}

.topbarImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    cursor: pointer;
    object-fit: cover;
}

.topbarIconBadge {
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: #db6ab9;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;

}
