.sidebarWrapper {
    padding: 20px;
}

.sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 4px 6px;
    cursor: pointer;
    box-shadow: 4px 1px 11px -5px #7a7a7a;
    border-radius: 13px;
    transition: all 0.3s;
}

.sidebarListItem:hover {
    box-shadow: none;
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}

.sidebarListItemNoLink {
    color :gray;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 4px 6px;
    box-shadow: 4px 1px 11px -5px #7a7a7a;
    border-radius: 13px;
    transition: all 0.3s;
}


